import React, {useRef, useEffect} from 'react';
import {TweenMax, Power3} from 'gsap';
import scrollTo from 'gatsby-plugin-smoothscroll';
import BannerAnimation from "../components/banner-animation.js";
function HeroBanner() {
  let waveBg = useRef(null);
  
  useEffect(() => {
    TweenMax.to(
      waveBg,
      1.5,
      {
        opacity: 0.3,
        y: -21,
        repeat: -1, 
        yoyo: true,
        ease: Power3.easeOut,
        delay: 1.2
      }
    )
    
  }, []);
  
  return (
    <>
    <section className="hero-banner">
      <div className="hero-banner__ellipse"></div>
      <div className="hero-banner__wave-bg"></div>
      <div className="hero-banner__wave-bg2" ref={el => {waveBg = el}}></div>
      <div className="container-fluid">
        <div className="row align-items-center">
          <div className="col">
            <div className="hero-banner__cnt">
              <h1
                data-aos='fade-right'
                data-aos-delay="600"
                data-aos-offset="50"
              >Secure <span>Cyberspace</span> <br />
               and 
                  <svg viewBox="0 0 200 80">
                    <text x="50%" y="50%" dy=".35em" textAnchor="middle">
                      Future
                    </text>
                  </svg>
              </h1>
              <p
                data-aos='fade-right'
                data-aos-delay="800"
                data-aos-offset="50"
              >Overwatch Security, Inc. is a <span>single platform managed security service</span> that aligns <span>organization's cybersecurity strategies with business objectives</span>, by integrating <span>cutting edge technology with strategic expertise.</span></p>
              <button onClick={() => scrollTo('#aboutSection')} className="btn btn--lg btn--stroke btn--stroke-white"
                data-aos='fade-right'
                data-aos-delay="1000"
                data-aos-offset="50"
              >Learn more</button>
            </div>
          </div>
          <div className="col">
            
            <div className="hero-banner__img">
              <BannerAnimation />
            </div>
          </div>
        </div>
      </div>
      <div className="scroll">
        <div className="scroll__text">
          <span>s</span>
          <span>c</span>
          <span>r</span>
          <span>o</span>
          <span>l</span>
          <span>l</span>
        </div>
      </div>
    </section> 
    </>
  )
}



export default HeroBanner