import React, {useRef, useEffect} from 'react';
import {TweenMax, Power3} from 'gsap';
import HeroBannerImg from "../assets/images/animation/banner-img.svg";
import ImageTwo from "../assets/images/animation/3d.svg";
import ImageThree from "../assets/images/animation/overlay-bg.svg";

function BannerAnimation() {
  let animationImg = useRef(null);
  let animationOne = useRef(null);
  let animationTwo = useRef(null);
  let animationThree = useRef(null);
  let animationFour = useRef(null);
  let animationFive = useRef(null);
  let box2Cnt = useRef(null);
  
  useEffect(() => {
    TweenMax.to(
      animationImg,
      1,
      {
        opacity: 1,
        y: 20,
        ease: Power3.easeOut,
        delay: 0.8
      }
    )
    
  }, []);

  useEffect(() => {
    TweenMax.to(
      animationOne,
      1,
      {
        opacity: 1,
        y: -21,
        repeat: -1, 
        yoyo: true,
        ease: Power3.easeOut,
        delay: 1.5
      }
    )
    
  }, []);

  useEffect(() => {
    TweenMax.to(
      animationTwo,
      1,
      {
        opacity: 1,
        y: -10,
        ease: Power3.easeOut,
        delay: 2.5
      }
    )
    
  }, []);

  useEffect(() => {
    TweenMax.to(
      animationThree,
      1,
      {
        opacity: 0.6,
        y: 50,
        ease: Power3.easeOut,
        delay: 3.5
      }
    )
    
  }, []);

  useEffect(() => {
    TweenMax.to(
      animationFour,
      1,
      {
        opacity: 1,
        x: -50,
        ease: Power3.easeOut,
        delay: 4.5
      }
    )
    
  }, []);

  useEffect(() => {
    TweenMax.to(
      animationFive,
      1,
      {
        opacity: 1,
        y: 50,
        ease: Power3.easeOut,
        delay: 5
      }
    )
    
  }, []);
  useEffect(() => {
    TweenMax.to(
      box2Cnt,
      2,
      {
        opacity: 1,
        repeat: -1,
        ease: "slow(0.7, 0.7, false)",
        delay: 5.5
      }
    )
    
  }, []);
  return (
    <>
    <div className="animation-wrap">
      <img className="animation-wrap__img" ref={el => {animationImg = el}} src={HeroBannerImg} alt="" />
      <div className="animation-wrap__animation-item animation-wrap__animation-item--one" ref={el => {animationOne = el}}>
        <svg width="131" height="110" viewBox="0 0 131 110" fill="none">
          <path d="M1 72.1L65.3 34.9L129.7 72.1L65.3 109.2L1 72.1Z" stroke="white" strokeWidth="0.9274" strokeMiterlimit="10"/>
          <path d="M1 60.8L65.3 23.6L129.7 60.8L65.3 97.9L1 60.8Z" stroke="white" strokeWidth="0.9274" strokeMiterlimit="10"/>
          <path d="M1 49.5L65.3 12.3L129.7 49.5L65.3 86.6L1 49.5Z" stroke="white" strokeWidth="0.9274" strokeMiterlimit="10"/>
          <path d="M1 38.2L65.3 1L129.7 38.2L65.3 75.3L1 38.2Z" stroke="white" strokeWidth="0.9274" strokeMiterlimit="10"/>
        </svg>
      </div>
      <div className="animation-wrap__animation-item animation-wrap__animation-item--two" ref={el => {animationTwo = el}}>
        <img className="rotate-img rotate-img--one" src={ImageTwo} alt="" />
        <img className="rotate-img rotate-img--two" src={ImageTwo} alt="" />
        <img className="rotate-img rotate-img--three" src={ImageTwo} alt="" />
        <img className="rotate-img rotate-img--four" src={ImageTwo} alt="" />
      </div>
      <div className="animation-wrap__animation-item animation-wrap__animation-item--three" ref={el => {animationThree = el}}>
        <img src={ImageThree} alt="" />
      </div>
      <div className="animation-wrap__animation-item animation-wrap__animation-item--four" ref={el => {animationFour = el}}>
        <div className="box-cnt"></div>
      </div>
      <div className="animation-wrap__animation-item animation-wrap__animation-item--five" ref={el => {animationFive = el}}>
        <div className="box2-cnt" ref={el => {box2Cnt = el}}></div>
      </div>
      
    </div>
    </>
  )
}

export default BannerAnimation;