import React, {Component} from 'react';
import {} from 'react-bootstrap';
import scrollTo from 'gatsby-plugin-smoothscroll';
import SiteLogo from "../assets/images/overwatch-logo-white.svg";

class  SiteNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {showNavigation: false};
    this.handleClick = this.handleClick.bind(this);
  }

// function to handle the click
 handleClick() {
  this.setState(prevState => ({
    showNavigation: !prevState.showNavigation
  }));
}
  render(){ 
    const mainMenu = (
      <div className="site-navigation__bg">
        <div className="hero-banner__ellipse"></div>
        <div className="scroll"></div>
        <header className="site-header site-navigation__bg__header">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-6">
                <div className="site-logo">
                  <a href="/">
                    <img src={SiteLogo} alt="" />
                  </a>
                </div>
              </div>
              <div className="col-md-6">
                <div className="site-navigation__bg__header__icon">
                  <div className="site-navigation__btn site-navigation__bg__header__btn" onClick={this.handleClick}>
                    <span></span>
                    <span></span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <div className="menu">
                <div className="menu__list" onClick={this.handleClick}>
                  <a onClick={() => scrollTo('#aboutSection')}>About</a>
                  <div className="menu__list__sub-menu">
                    <div className="menu__list__sub-menu__title">
                      <span>A</span>
                      <span>b</span>
                      <span>o</span>
                      <span>u</span>
                      <span>t</span>
                    </div>
                    <div className="menu__list__sub-menu__cnt menu__list__sub-menu__cnt--bg">
                      <div className="row justify-content-md-center">
                        <div className="col-md-12">
                          <p>We are a cybersecurity startup that partners with enterprises and organizations to address their critical cybersecurity risks. We provide our clients access to cutting edge cybersecurity solutions tailored to their needs, enabling them to maintain a robust security posture.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="menu__list" onClick={this.handleClick}>
                  <a onClick={() => scrollTo('#serviceSection')}>Service</a>
                  <div className="menu__list__sub-menu">
                    <div className="menu__list__sub-menu__title">
                      <span>S</span>
                      <span>e</span>
                      <span>r</span>
                      <span>v</span>
                      <span>i</span>
                      <span>c</span>
                      <span>e</span>
                    </div>
                    <div className="menu__list__sub-menu__cnt menu__list__sub-menu__cnt--bg">
                      <div className="row justify-content-md-center">
                        <div className="col-md-12">
                          <h4>Managed Detect and Respond</h4>
                          <p>The purpose of a Managed detect and respond (MDR) is to identify, investigate,prioritize, and resolve issues that could affect the security of an organizations critical infrastructure and data. A well-developed and well-run MDR can perform real-time threat detection and incident response, with analysts that can deliver rapid security intelligence to stakeholders and senior management, identifying when an attack starts, who is attacking, how the attack is being conducted, and what data or systems are being compromised.</p>
                          <button onClick={() => scrollTo('#serviceMDR')} className="btn btn--md btn--yellow">Read More</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="menu__list" onClick={this.handleClick}>
                  <a onClick={() => scrollTo('#productSection')}>Product</a>
                  <div className="menu__list__sub-menu">
                    <div className="menu__list__sub-menu__title">
                      <span>P</span>
                      <span>r</span>
                      <span>o</span>
                      <span>d</span>
                      <span>u</span>
                      <span>c</span>
                      <span>t</span>
                    </div>

                    <ul className="menu__list__sub-menu__cnt">
                      <li><a onClick={() => scrollTo('#productOne')}><div className="menu__list__sub-menu__cnt__num">01</div>Security information and event management</a></li>
                      <li><a onClick={() => scrollTo('#productTwo')}><div className="menu__list__sub-menu__cnt__num">02</div>Process Automation Manager</a></li>
                      <li><a onClick={() => scrollTo('#productThree')}><div className="menu__list__sub-menu__cnt__num">03</div>Identity accessmanagement Solution</a></li>
                      <li><a onClick={() => scrollTo('#productFour')}><div className="menu__list__sub-menu__cnt__num">04</div>Fraud and Risk Intelligence</a></li>
                      <li><a onClick={() => scrollTo('#productFive')}><div className="menu__list__sub-menu__cnt__num">05</div>Governance and Compliance Solution</a></li>
                    </ul>
                  </div>
                </div>
                <div className="menu__list" onClick={this.handleClick}>
                <a onClick={() => scrollTo('#contactSection')}>Contact</a>
                <div className="menu__list__sub-menu">
                    <div className="menu__list__sub-menu__title">
                      <span>C</span>
                      <span>o</span>
                      <span>n</span>
                      <span>t</span>
                      <span>a</span>
                      <span>c</span>
                      <span>t</span>
                    </div>
                    <div className="menu__list__sub-menu__cnt menu__list__sub-menu__cnt--bg">
                      <div className="row justify-content-md-center">
                        <div className="col-md-6">
                          <h4>Nepal</h4>
                          <ul>
                            <li><i className="icon icon-es-location-pin"></i> Sama marg, Kathmandu</li>
                            <li><i className="icon icon-es-mail"></i> mail.np@overwatchsecurity.tech</li>
                            <li><i className="icon icon-es-phone"></i> +977-9844041389</li>
                          </ul>
                        </div>
                        <div className="col-md-6">
                          <h4>USA</h4>
                          <ul>
                            <li><i className="icon icon-es-location-pin"></i> Sama marg, Kathmandu</li>
                            <li><i className="icon icon-es-mail"></i> mail.np@overwatchsecurity.tech</li>
                            <li><i className="icon icon-es-phone"></i> +977-9844041389</li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <footer className="site-navigation__bg__footer">
         2019 - 2020. All Rights Reserved.  <a href="#">Privacy Policy</a>
        </footer>
      </div>
    );

    return (
      <>
    <nav className="site-navigation">
      <div className="site-navigation__btn site-navigation__btn--sticky" onClick={this.handleClick}>
        <span></span>
        <span></span>
        <span></span>
      </div>
      
      {this.state.showNavigation ? mainMenu : ''}
    </nav>
  </>
    );
  }
}

export default SiteNavigation