import React from 'react';
import Helemt from 'react-helmet';
import useSiteMetadata from '../hooks/use-sitemetadata';
import 'aos/dist/aos.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/sass/overwatchsecurity/overwatchsecurity.scss';
import {} from 'react-bootstrap';
import SiteHeader from '../components/site-header.js';
import HeroBanner from '../components/hero-banner.js';
import AboutSection from '../sections/about.js';
import ServiceSection from '../sections/services.js';
import OurProcess from '../sections/our-process.js';
import Product from '../sections/product.js';
import OurPartners from '../sections/our-partners.js';
import Contact from '../sections/contact.js';
// import GotQuestion from '../sections/got-question.js';
import SiteFooter from '../components/site-footer.js';

const HomeLayout = () => {
  const {title, description } = useSiteMetadata();
  
  return (

    <>
    <Helemt>
      <html lang="en" />
      <title>{title}</title>
      <meta name="dec" content={description}></meta>
    </Helemt>
    <SiteHeader />
    <HeroBanner />
    <AboutSection />
    <ServiceSection />
    <OurProcess />
    <Product />
    <OurPartners />
    <Contact />
    {/* <GotQuestion /> */}
    <SiteFooter />
    </>
  );
}

export default HomeLayout;
