import React from 'react';
import AOS from 'aos';
import OurProcessImage from '../assets/images/our-process.svg';

class OurProcess extends React.Component {
  constructor(props) {
    super(props);
    AOS.init();
  }

  render() {
    return (
      <>
        <section className="section section--our-process">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <div className="section--our-process__bg">
                  <div className="section__title">
                    <span>P</span>
                    <span>r</span>
                    <span>o</span>
                    <span>c</span>
                    <span>e</span>
                    <span>s</span>
                    <span>s</span>
                  </div>
                  <h2
                    className="title-border-btm"
                    data-aos="fade-up"
                    data-aos-delay="1000"
                    data-aos-offset="100"
                  >
                    Our Process
                  </h2>
                  <p
                    data-aos="fade-up"
                    data-aos-delay="1200"
                    data-aos-offset="100"
                  >
                    We are a cybersecurity startup that partners with
                    enterprises and organizations to address their critical
                    cybersecurity risks.
                  </p>
                  <div className="section--our-process__img">
                    <img
                      src={OurProcessImage}
                      alt=""
                      data-aos="zoom-in"
                      data-aos-delay="1400"
                      data-aos-offset="100"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default OurProcess;
