import React from 'react';
import partnerRsa from '../assets/images/partners/rsa-partner.png';
const OurPartners = () => {
 
    return (
      <section className="section section--our-partners">
        <div className="container-fluid">
          <div className="row justify-content-md-center">
            <div className="col-md-10">
              <h2 className="title-border-btm title-border-btm--yellow"
                data-sal="slide-in"
                data-sal-duration="50"
                data-sal-delay="200"
              >Our Partners</h2>
              <p
                data-sal="slide-in"
                data-sal-duration="50"
                data-sal-delay="300"
              >Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</p>

              <ul className="section--our-partners__list">
                <li
                  data-sal="slide-in"
                  data-sal-duration="50"
                  data-sal-delay="400"
                ><a><img src={partnerRsa} alt="" /></a></li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    )
  
}

export default OurPartners