                    <path d="M112.629 150.791C107.507 141.339 111.038 129.536 120.515 124.427L179.218 92.7854C188.695 87.6772 200.529 91.1986 205.651 100.651C210.772 110.103 207.242 121.907 197.765 127.015L139.062 158.657C129.585 163.765 117.751 160.244 112.629 150.791Z" fill="white" fillOpacity="0.15"/>
